$dark-bg:#06090f;
$dark-title: #242c40;
$dark-btn-color:#21262d;
$dark-text-color:#afb6be;
$dark-border:#242C40;
$dark-input:#0d1117;
$dark-input-border:#3f4247;

.strike-div{
  padding: 9px 34px 16px 32px;
  margin-top: 0px;
  border-bottom: 0.5px solid #f0f3f7;
  width: 100%;
}
s, strike{text-decoration:none;position:relative;
  top:3px;

}
s::before, strike::before {
  top: 50%; /*tweak this to adjust the vertical position if it's off a bit due to your font family */
  background:black; /*this is the color of the line*/
  opacity:.7;
  content: '';
  width: 105%;
  position: absolute;
  height:.2em;
  z-index: 2;
  border-radius:.1em;
  left: -2%;
  white-space:nowrap;
  display: block;
  transform: rotate(0deg);
}

.important-icon-dark{
  width: 25px;
  cursor: pointer;
  right: 45px;
  top:5px;
  position: absolute;
  color: "white"
}
.important-icon-light{
  width: 25px;
  cursor: pointer;
  right: 45px;
  top:5px;
  position: absolute;
}
.strike-icon-dark{
  width: 25px;
  cursor: pointer;
  right: 25px;
  top: 5px;
  position: absolute;
}
.strike-icon-light{
  width: 25px;
  cursor: pointer;
  right: 25px;
  top: 5px;
  position: absolute;
}
.check-icon{
  position: absolute;
  top: 9px;
  right: 33px;
}
.delete-icon{
  position: absolute;
  top: 9px;
  right: 10px;
}
strike::after,s::after {
  top: 45%; /*tweak this to adjust the vertical position if it's off a bit due to your font family */
  background: #fffdfd;
  opacity: .9;
  z-index: 1;
  content: '';
  width: 105%;
  position: absolute;
  height:.3em;
  border-radius:.1em;
  left: -2%;
  white-space:nowrap;
  display: block;
  transform: rotate(0deg);
}
s.straight::before, strike.straight::before{transform: rotate(0deg);left:-1%;width:102%;}

.bold-font{
