}
.heading{ display: flex;
  justify-content: center;
  align-items: center;
  outline: none;
  margin: 1rem 0;
  font-size: 20px;}
.dark-mode{
  background: $dark-bg;
  .heading,.header-wrapper,.menu-items,.link-box,.react-grid-layout,.card-heading,.card-content{
    background: $dark-bg;
  }
  #root>div{
    background: $dark-bg;
  }
  .link-box{
    border: 1px solid $dark-border !important;
  }
  .title{
    color:$dark-text-color !important;
  }
  .article{
    color: $dark-text-color  !important;
  }
  .normal-btn{
    //margin-top: 7px;
  }
  .card{
    border: 1px solid $dark-border;
  }
  .menu-icon,.fa-icon,.footer-icon{
    filter: invert(1);
  }
  .important-icon-dark{
    visibility: visible;
  }
  .important-icon-light{
    visibility: hidden;
  }
  .rss-big-icon {
      margin: 0;
  }
  .header-wrapper{
    background: $dark-bg !important;
  }
  .menu-items{
    button{
      div{
        background: $dark-bg !important;
        background-color: $dark-bg !important;
      }
    }
  }
  .header-wrapper {
    background: $dark-bg !important;
  }
  .list-item{
    border-bottom: none !important;
  }
  input:not(.list-item){
    background-color: $dark-input !important;
    border-color:$dark-input-border !important;
  }
  .card-heading{
    border-radius: 6px 6px 0px 0px !important;
  }
  .card-content{
    border-radius: 0px 0px 6px 6px !important;
  }
  .heading{
    color:#d4d4d4
  }

  .strike-div{
    border-bottom: 0.5px solid #100505 !important;
  }

  $background: #999999, #6e6c6c, #131313,#000000;
  $textColor: #000000,#ffffff,#ffffff, #ffffff;

  @for $i from 1 through length($background) {
    $bg: nth($background, $i);
    $color: nth($textColor, $i);
    $class:$i - 1;
    .shade-#{$class} {
      background: $bg !important;
      color: $color !important;
      border-bottom: 0.5px solid #151313;

    }

    .shade-color-#{$class} {
      color: $color !important;
    }

  }

}


