.card-wrapper {
  height: 100%;
  position: relative;
  box-sizing: border-box;

  .delete-icon{
    position: absolute;
    right: 10px;
    top: 10px;
    z-index: 100;
  }
}

.card-container {
  position: absolute;
  box-sizing: inherit;
  border-radius: 3px;
  height: 100%;
  width: 100%;

  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-flex: 1;
  -webkit-flex: 1 1 0;
  -ms-flex: 1 1 0px;
  flex: 1 1 0;
}

.card-content {
  width: 100%;
  box-sizing: border-box;
  -webkit-box-flex: 1;
  -webkit-flex-grow: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  height: -webkit-calc(100% - 52px);
  height: calc(100% - 52px);
}

.card-height-helper {
  display: block;
  height: 100%;
}

.card {
  border: 1px solid #dadce0;
  border-radius: 8px;
  background: #ffffff;
  height: 100%;
}
.options {
  padding-right: 10px;
}
.card-heading {
  font-weight: 500;
  font-size: 1.125rem;
  color: #797f82;
  padding-left: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
  cursor: move;

  display: flex;
  justify-content: space-between;
}

.height-layer {
  height: 100%;
}

.baron {
  overflow: auto !important;
  overflow-x: hidden !important;
}

.baron.panel-content--scrollable {
  width: -webkit-calc(100% - 0px);
  width: calc(100% - 0px);
}
.panel-height-helper {
  display: block;
  height: 100%;
}
.baron__clipper {
  position: relative;
  overflow: hidden;
}
.baron {
  overflow: hidden;
}
