.bookmark-wrapper {
  .bookmark-list {
    overflow: auto;
  }
  ul{margin:0px;padding:0px;}

  .input-list-item {
    list-style: none;
    padding: 0 !important;
    outline: none;

    &:hover{
      img{
        visibility:visible
      }
    }


    .list ul {
      padding: 0;
    }

    .list-item {
      width: 100%;
      border: none;
      box-sizing: border-box;
      background: transparent;

      padding: 15px 34px 16px 32px;
      //border-top: 0.5px solid #f0f3f7;
      margin-top: 0px;

      &:hover {
        svg {
          visibility: visible;
          padding: 3px;
        }

        path {
          fill: #d9dadb;
        }
      }

      &:last-of-type {
        border-bottom: 0.5px solid #f0f3f7;
      }
    }
  }
}