.header-wrapper {
  .heading {
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    margin: 1rem 0;
    font-size: 20px;
  }

  .menu-icon {
    width: 30px;
    height: 30px;
  }


  .footer-icon {
    width: 20px;
    height: 20px;
  }

  .last-item {
    position: absolute;
    top: 10px;
    right: 0px;
  }

  .dotted-bg {
    //width: 86px;
    height: 40px;
    position: absolute;
    right: 64px;
    top: 17px;
    -webkit-animation: slide 0.5s forwards;
    -webkit-animation-delay: 2s;
    animation: slide 0.5s forwards;
    animation-delay: 2s;

    &:hover {
      width: 200px;
      background: white;
      color: white;
      //border: none !important;
      //border-color: transparent !important;
      svg {
        color: #282c34;
      }
    }
  }
  .fa-icon {
    font-size: 2rem;
    path {
      color: #d5d5de;
    }
  }

  .centered {
    justify-content: center;
    align-items: center;
  }

  .menu-items {
    padding: 5%;
    margin: 10%;
    display: flex;
    svg {
      margin-right: 10px;
      margin-left: 10px;
    }
  }

  .normal-btn {
    background-color: red;
    background: none;
    border: none;
  }
  .rss-big-icon {
    //margin-top: 5px !important;
    //width: 55px;
  }
  .notes-icon-big {
    width: 65px !important;
    left: 13px;
    position: relative;
  }

  .setting-icon{
    right: 28px;
    top: 20px;
  }
}
