.list-wrapper {
  .move-handler{
    width: 32px;
    position: absolute;
    top: 6px;
    filter: opacity(0.5);
    visibility: hidden;
  }

  .list ul {
    padding: 0;
  }
  svg,img {
    visibility: hidden;
  }


  .actions {
    &:hover {
      svg,img {
        visibility: visible;
        //padding: 3px;
      }

      img{
        visibility: visible;
      }
      path {
        fill: #d9dadb;
      }
    }
  }

  .list-item {
    padding: 15px 34px 16px 32px;
    width: 100%;
    border: none;
    box-sizing: border-box;
    background: transparent;
    outline: 0;
    border: 0;
    outline: none;

    //border-top: 0.5px solid #f0f3f7;
    margin-top: 0px;
    &:last-of-type {
      border-bottom: 0.5px solid #f0f3f7;
    }
  }


  .importance {
    right: 14px;
    top: 10px;
  }

  .action-items {
    padding: 6px 5px;
    margin-left: 2px;
    top: 8px;
    right: 4px;
  }
}

.input-list-item {
  list-style: none;
  padding: 0 !important;
  outline: none;
  svg {
    visibility: hidden;
  }

  .list ul {
    padding: 0;
    li{
      margin-bottom: 2px;
      background: #0d1117;
    }
  }
  .list-item {
    width: 100%;
    border: none;
    box-sizing: border-box;
    background: transparent;
    padding: 15px 34px 16px 32px;

    //border-top: 0.5px solid #f0f3f7;
    margin-top: 0px;

    &:hover {
      svg {
        visibility: visible;
        //padding: 3px;
      }

      path {
        fill: #d9dadb;
      }
    }

    &:last-of-type {
      border-bottom: 0.5px solid #f0f3f7;
    }
  }
  .importance {
    right: 14px;
    top: 10px;
  }

  .action-items {
    padding: 6px 5px;
    margin-left: 2px;
    top: 8px;
    right: 4px;
  }
}
