.link-box-wrapper {
  margin-left: 20px;

  svg{
    path {
      fill: #d9dadb;
    }
  }
  &:hover {
    svg {
      visibility: visible;
      padding: 3px;
      path {
        fill: #d9dadb;
      }
    }

    img{
      visibility: visible;
    }
    path {
      fill: #d9dadb;
    }
  }
  .link-box {
    text-decoration: none;
    display: flex;
    border: 1px solid #ffffff;
    height: auto;
    font-size: 0.7rem;
    background: #ffffff;
    padding: 5px;
    cursor: pointer;
    margin-right: 10px;


    .icon {
      img {

        height: 63px;
        width: 63px;
        border-radius: 4px 0px 0px 4px;
        object-fit: cover;
        padding-right: 10px;
      }
    }
    .title {
      flex: 80;
      color: #000000;
      -webkit-line-clamp: 1;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;

      display: flex;
      justify-content: space-between;
    }
    .sub-title {
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      padding-top: 2px;
      color: #7b8183;
      -webkit-box-orient: vertical;
    }
  }

  .action-item {
    position: absolute;
    right: 22px;
    top:4px;
    &:hover {
      svg {
        visibility: visible;
        padding: 3px;
      }

      img{
        visibility: visible;
      }
      path {
        fill: #d9dadb;
      }
    }
  }
}
