.new-wrapper {
  display: flex;
  padding: 5px;
  > div {
    flex: 0.97;
  }
  .add-btn {
    margin-left: 5px;
    background-color: #423b2e;
    background-image: none;
    border: 1px solid transparent;
    border-radius: 3px;
    box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    display: inline-block;
    font-family: "Source Sans Pro", sans-serif;
    font-size: 12px;
    font-weight: 700;
    line-height: 1.5;
    margin-bottom: 0;
    padding: 4px 10px;
    text-align: center;
    text-decoration: none;
    touch-action: manipulation;
    user-select: none;
    vertical-align: middle;
    white-space: nowrap;
  }
}
